/*底部栏目切换栏*/
.bottomBar[data-v-c406c2c6] {
  border-top: 1px solid #dfdde8;
  height: 50px;
  background: #f7f7fa;
  width: 100%;
  position: absolute;
  z-index: 5;
  bottom: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.bottomBar .item[data-v-c406c2c6] {
  width: 25%;
  height: 100%;
}
.bottomBar .item .ico[data-v-c406c2c6] {
  width: 100%;
  text-align: center;
  color: #999;
  height: 30px;
  line-height: 1.6;
  font-size: 1.4rem;
}
.bottomBar .item .name[data-v-c406c2c6] {
  width: 100%;
  text-align: center;
  color: #999;
  font-size: 10px;
  line-height: 20px;
  height: 20px;
}
.bottomBar .active .ico[data-v-c406c2c6],
.bottomBar .active .name[data-v-c406c2c6] {
  color: blueviolet;
}



