
body {
  height: 100%;
  width: 100%;
  background-color: #f4f4f4;
  overflow: hidden;
}
.root-box {
  height: 100%;
  width: 100%;
}
/*标题栏按钮样式*/
.titleButton {
  height: 45px;
  width: 45px;
  font-size: 1.4rem;
}
a:visited,
a:link,
a:hover,
a:active {
  color: black;
}
/*栏目 通知 激活样式*/
.noticeActive .notice-box {
  width: 100%;
  color: blueviolet;
  display: block;
}
.noticeActive .notice .ico,
.noticeActive .notice .name {
  color: blueviolet !important;
}
/*栏目 应用 激活样式*/
.appActive .app-box {
  width: 100%;
  color: blueviolet;
  display: block;
}
.appActive .app .ico,
.appActive .app .name {
  color: blueviolet !important;
}
/*栏目 通讯录 激活样式*/
.contactsActive .contacts-box {
  width: 100%;
  display: block;
}
.contactsActive .contacts .ico,
.contactsActive .contacts .name {
  color: blueviolet !important;
}
/*栏目 我 激活样式*/
.meActive .personal-information {
  width: 100%;
  display: block;
  color: blueviolet;
}
.meActive .me .ico,
.meActive .me .name {
  color: blueviolet !important;
}
/*进入应用市场样式*/
.appStoreActive {
  /*将底部选项卡占用位置释放*/
}
.appStoreActive .app-store-box {
  width: 100%;
  display: block;
}
.appStoreActive .content {
  bottom: 0px;
}
/*显示面板*/
.content {
  background: #f4f4f4;
  width: 100%;
  z-index: 10;
  bottom: 51px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
/*搜索框样式*/
.vux-search-box .weui-search-bar {
  background-color: #f8f8f8;
}
.vux-search-box .weui-search-bar:before {
  border-top: 0;
}
.vux-search-box .weui-search-bar__label {
  background: #dbdbdb;
}



