/*标题栏样式*/
.title-bar[data-v-06d79354] {
  height: 45px;
  line-height: 45px;
  background: #f8f8f8;
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #dfdde8;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  /*标题栏按钮样式*/
}
.title-bar .title[data-v-06d79354] {
  height: 45px;
  line-height: 45px;
  font-size: 18px;
  font-family: -apple-system-font, Helvetica Neue, sans-serif;
  width: calc(100% - 90px);
}
.title-bar .titleButton[data-v-06d79354] {
  height: 45px;
  width: 45px;
  font-size: 1.4rem;
  color: #1865ff;
}



